@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap);

.sidebar{position:fixed;left:0;top:0;width:260px;background-color:#282828;height:100vh;padding:0 25px}.sidebar__logo{text-transform:uppercase;border-bottom:1px solid rgba(136,136,136,0.5);height:50px;align-items:center;display:flex}.sidebar__icon{width:30px;display:flex;font-size:14px;margin-right:15px;justify-content:center}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
*{
	box-sizing: border-box;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
	color: inherit;
    text-decoration: none;
}
body{background-color:#222;color:#888;font-family:'Roboto', sans-serif}.app .content{width:calc(100% - 260px);margin-left:260px;padding-left:25px}

